import React, { Component, Suspense } from 'react';
import { Link } from 'react-router-dom';
import DisplayUrl from '../../../PublishCommonComponents/DisplayUrl';
import { DisplayURL, UGC_SUITE_WALL_PREFIX } from '../../../../../Services/Api'
class OtherSignageUrl extends Component {

    render() {
        const { data } = this.props
        return <div className="card text-decoration-none">
            <div className="card-body p-lg-7">
                <div className="m-auto">
                    <div className="embed_code">
                        <h5 className="mb-5 fw-bolder">Steps To Display Social wall With Digital signage software</h5>
                        <h6>Step 1 : Copy The Display Wall URL</h6>
                        <DisplayUrl url={`${DisplayURL}${UGC_SUITE_WALL_PREFIX}${data && Object.keys(data).length > 0 ? data.wallId : null}`} />
                        <h6 className="mb-5">Step 2: Add The Social Wall To Digital Signage Software In The Following Way</h6>
                        <ul className="list-unstyled list-points fs-7">
                            <li className="mb-3">Start by Logging to your digital <span className="fw-bold">signage account</span></li>
                            <li className="mb-3">Next, find the option to embed the URL/Webpage</li>
                            <li className="mb-3">Lastly, copy and paste the display <span className="fw-bold">URL</span> into the URL field and click on <span className="fw-bold">Save</span></li>
                            <li className="mb-3">Congratulations, you've successfully added the social wall to the opted digital signage software!</li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    }
}

export default OtherSignageUrl;